///src/components/ProjectMap.js


import React, { useState , useEffect} from 'react';
import { Modal, Button, Form } from 'react-bootstrap';
import Sidebar from './Sidebar';
import Column from './Column';
import blankCards from '../util/BlankCards';
import { generateUniqueId } from '../util/helpers'; 

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';  // Import the styles or use your own custom CSS
import { db } from '../util/firebase-config';
import { collection, doc, getDocs, getDoc, setDoc, updateDoc, deleteDoc,onSnapshot } from "firebase/firestore"; 


console.log(db)
const modules = {
    toolbar: [
        [{ header: '1' }, { header: '2' }, { font: [] }],
        [{ size: [] }],
        ['bold', 'italic', 'underline', 'strike', 'blockquote'],
        [{ list: 'ordered'}, { list: 'bullet' }],
        ['link', 'image', 'video'],
        ['clean']
    ],
};

const formats = [
    'header', 'font', 'size',
    'bold', 'italic', 'underline', 'strike', 'blockquote',
    'list', 'bullet',
    'link', 'image', 'video'
];

const ProjectMap = () => {
    const [showModal, setShowModal] = useState(false);
  const [currentCard, setCurrentCard] = useState(null);
  const [cardsInMap, setCardsInMap] = useState(() => {
    //const storedCards = localStorage.getItem('cardsInMap');
    //return storedCards ? JSON.parse(storedCards) : [];
    return []
});
const [isLocalUpdate, setIsLocalUpdate] = useState(false);
const [cards, setCards] = useState([]);

// Assuming the details are stored as a stringified Delta object
useEffect(() => {
    const mapRef = doc(db, 'maps', 'ahshd88as8hd717182837');
    const cardsCollectionRef = collection(mapRef, 'cards');

    const unsubscribe = onSnapshot(cardsCollectionRef, (snapshot) => {
        const cardsData = snapshot.docs.map(doc => ({
            ...doc.data(),
            id: doc.id,
            details: JSON.parse(doc.data().details || '{"ops":[]}') // Parse JSON safely
        }));
        setCardsInMap(cardsData);
    }, error => {
        console.error("Failed to fetch cards:", error);
    });

    return () => unsubscribe();  // Clean up the subscription
}, []);


  const stages = ['empathy', 'define', 'ideate', 'prototype', 'test'];
  const cardsByStage = (stage) => blankCards.filter(card => card.stage === stage);
  const columnClasses = {
    empathy: "text-primary bg-light",
    define: "text-success bg-light",
    ideate: "text-info bg-light",
    prototype: "text-warning bg-light",
    test: "text-danger bg-light"
  };

// Function to handle adding a new card
const onAddCard = (templateCard) => {
    setCurrentCard({
        ...templateCard,
        id: generateUniqueId(),
        details: JSON.stringify({ ops: [] }), // Initialize with empty ops array
        isNew: true  // Flag new cards explicitly
    });
    setShowModal(true);
};


// Function to save a new card to Firestore
const saveNewCard = async () => {
    if (currentCard && currentCard.title && currentCard.details) {
        const newCard = {
            ...currentCard,
            id: generateUniqueId(),  // Ensure it gets a final unique ID
            isNew: false,
            details: JSON.stringify(currentCard.details.ops) // Serialize the details
        };
        const newCardRef = doc(collection(db, 'maps', 'ahshd88as8hd717182837', 'cards'));
        await setDoc(newCardRef, newCard);
        // Do not update local state; let Firebase listener handle it
        setShowModal(false);
        setCurrentCard(null);
    }
};

/*
const saveNewCard = () => {
    if (currentCard && currentCard.title && currentCard.details) {
        const newCard = {
            ...currentCard,
            id: generateUniqueId(),  // Ensure it gets a final unique ID
            isNew: false  // Remove the new flag after saving
        };
        setCardsInMap(prevCards => [...prevCards, newCard]);
        setShowModal(false);
        setCurrentCard(null);
    }
};
*/


// Function to handle save changes, determines if it's a new card or an edited one
const handleSaveChanges = () => {
    if (currentCard.isNew) {
        saveNewCard();
    } else {
        saveEditedCard();
    }
};

// Function to update an existing card in Firestore
const saveEditedCard = async () => {
    const cardIndex = cardsInMap.findIndex(card => card.id === currentCard.id);
    if (cardIndex !== -1) {
        const cardRef = doc(db, 'maps', 'ahshd88as8hd717182837', 'cards', currentCard.id);
        const updatedCard = {
            ...currentCard,
            isNew: false,
            details: JSON.stringify(currentCard.details)  // Serialize the details
        };
        await updateDoc(cardRef, updatedCard);
        // Do not update local state; let Firebase listener handle it
        setShowModal(false);
    }
};

  const onEditCard = (cardId) => {
    const cardToEdit = cardsInMap.find(card => card.id === cardId);
    if (cardToEdit) {
      setCurrentCard(cardToEdit);
      setShowModal(true);
    }
  };
  
  const handleDeleteCard = (cardId) => {
    if (window.confirm('Are you sure you want to delete this card?')) {
        // Define the reference to the card document in Firestore
        const mapRef = doc(db, 'maps', 'ahshd88as8hd717182837');
        const cardRef = doc(mapRef, 'cards', cardId);

        try {
            // Delete the card document from Firestore
            deleteDoc(cardRef);

            // Filter out the deleted card from the local state to update the UI
            const updatedCards = cardsInMap.filter(card => card.id !== cardId);
            setCardsInMap(updatedCards);

            setShowModal(false); // Close the modal if it's open
        } catch (error) {
            console.error("Error deleting card:", error);
            alert("Failed to delete the card. Please try again.");
        }
    }
};


  // Load existing cards from localStorage on component mount
  /*
  useEffect(() => {
    const storedCards = localStorage.getItem('cardsInMap');
    if (storedCards) {
      setCardsInMap(JSON.parse(storedCards));
    }
  }, []);

*/


  /*
  // Update localStorage when cardsInMap changes
  useEffect(() => {
    localStorage.setItem('cardsInMap', JSON.stringify(cardsInMap));
  }, [cardsInMap]);
  */
  return (
    <div className="container-fluid">
        <div className="row">
           
            <Sidebar onAddCard={onAddCard} />
       
            <div className="col-md-10 d-flex project-map">
                {stages.map(stage => (
                    <Column key={stage} title={stage} stage={stage} cards={cardsInMap.filter(card => card.stage === stage)} onEditCard={onEditCard} />
                ))}
            </div>
        </div>
        <Modal show={showModal} onHide={() => setShowModal(false)}>
    <Modal.Header closeButton className={currentCard ? columnClasses[currentCard.stage] : ''}>
        <Modal.Title>{currentCard ? `Edit ${currentCard.title} Card` : 'Create Card'}</Modal.Title>
    </Modal.Header>
    <Modal.Body>
        <Form>
            <Form.Group className="mb-3" controlId="formCardTitle">
                <Form.Label>Title</Form.Label>
                <Form.Control
                    type="text"
                    placeholder="Enter title"
                    value={currentCard?.title || ''}
                    onChange={(e) => setCurrentCard({ ...currentCard, title: e.target.value })}
                />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formCardDetails">
    <Form.Label>Details</Form.Label>
    <ReactQuill
    theme="snow"
    modules={modules}
    formats={formats}
    value={typeof currentCard?.details === 'string' ? JSON.parse(currentCard?.details) : currentCard?.details}
    onChange={(content, delta, source, editor) => setCurrentCard({
        ...currentCard,
        details: editor.getContents() // Always store the Delta object
    })}
/>

</Form.Group>
        </Form>
    </Modal.Body>
    <Modal.Footer className="justify-content-between">
        {currentCard && !currentCard.isNew && (
            <Button variant="danger" onClick={() => handleDeleteCard(currentCard.id)}>
                <i className="fas fa-trash-alt"></i> {/* Trashcan icon */}
            </Button>
        )}
        <div>
            
        <Button variant="primary" onClick={handleSaveChanges}>
    <i className="fas fa-save"></i> {/* Save icon */}
</Button>
        </div>
    </Modal.Footer>
</Modal>

    </div>
);
};

export default ProjectMap;

